import React from "react"
import Card from "../components/home/card"
import Layout from "../components/layout/layout"
import case9 from "../images/home/case-study-9.jpg"
import case10 from "../images/home/case-study-10.jpg"
import case11 from "../images/home/case-study-11.jpg"
import case14 from "../images/home/case-study-14.jpg"
import case15 from "../images/home/case-study-15.jpg"
import case16 from "../images/home/case-study-16.jpg"
import "../components/home/style.css"

const SearchResult = () => {

  const cardData = [
    {
        cover: case14,
        heading: "Facebook Friend Icon Redesign",
        category : ["Meta", "Product Design"]   
    },
    {
        cover: case10,
        heading: "Expansive gender options",
        category : ["Bumble", "Product Design"]   
    },
    {
        cover: case11,
        heading: "Alt Text Prompts",
        category : ["Twitter", "Product Design"]   
    },
    {
        cover: case9,
        heading: "Inclusive Language Checker",
        category : ["microsoft", "Product Design"]   
    },
    {
        cover: case15,
        heading: "Safety settings for drivers",
        category : ["Uber", "Product Design"]   
    },
    {
        cover: case16,
        heading: "Gender inclusive forms",
        category : ["Government", "Product Design"]   
    }, 

]



  return (
    <Layout>
        <div className="search-result-page">
            <div className="container">
                <h2>Showing search results</h2>
                <div className="search-list-grid">
                    <Card cardData={cardData} />
                </div>
            </div>
        </div>
    </Layout>
  )
}

export default SearchResult

export const Head = () => <title>Search Result</title>
